import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { RequestsPageComponent } from './components/requests-page/requests-page.component';
import { GroupsResolver } from 'src/app/services/security/groups.resolver';

const routes: Routes = [
  {
    path: '',
    component: RequestsPageComponent,
    resolve: { groups: GroupsResolver },
    data: { animation: 'fadeAnimation', title: 'Заявки' }
  }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    providers: [GroupsResolver],
    exports: [RouterModule]
})
export class RequestsRoutingModule { }
